import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';

import { FormsModule } from '@angular/forms';
import { BLE } from '@ionic-native/ble';
//import { BluetoothSerial } from '@ionic-native/bluetooth-serial';
import { AndroidPermissions } from '@ionic-native/android-permissions';

import { HttpModule } from '@angular/http';

/* COMPONENT */
import { MyApp } from './app.component';
//import { ComponentsModule } from '../components/components.module';

//import { TabsPage } from '../pages/tabs/tabs';
import { TabsPageModule } from '../pages/tabs/tabs.module';
import { CollectionsModule } from '../providers/collections-data/collections-data.module';
/* IONIC NATIVE */
import { WheelSelector } from '@ionic-native/wheel-selector';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { NativeStorage } from '@ionic-native/native-storage';

/* PROVIDERS */

import { ToastCtrlProvider } from '../providers/toast-ctrl/toast-ctrl';
import { BleCtrlProvider } from '../providers/ble-ctrl/ble-ctrl';
import { BleCommandProvider } from '../providers/ble-command/ble-command';
import { DevicesDataProvider } from '../providers/devices-data/devices-data';
import { ScheduleDataProvider } from '../providers/schedule-data/schedule-data';
import { AppStateProvider } from '../providers/app-state/app-state';

@NgModule({
  declarations: [
    //NumberPickerComponent,
    //LightsChartComponent,
    /*editDevicePage,
    ModeDevicesPage,
    DevMode,editDevSettingPage,
    TabsPage,*/
    MyApp,
  ],
  exports:[
  //NumberPickerComponent,
   //LightsChartComponent
  ],
  imports: [
    //ComponentsModule,
    HttpModule,
    TabsPageModule,
    /*ModeSchedulePageModule,
    ModeManualModule,*/
    FormsModule,
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/ble-operator/ble-operator.module#BleOperatorPageModule', name: 'BleOperatorPage', segment: 'ble-operator', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mode-manual/mode-manual.module#ModeManualModule', name: 'ModeManual', segment: 'mode-manual', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mode-devices/mode-devices.module#ModeDevicesPageModule', name: 'ModeDevicesPage', segment: 'mode-devices', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mode-schedule/mode-schedule.module#ModeSchedulePageModule', name: 'ModeSchedulePage', segment: 'mode-schedule', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/collections/collections.module#CollectionsPageModule', name: 'CollectionsPage', segment: 'collections', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/start/start.module#StartPageModule', name: 'StartPage', segment: 'start', priority: 'low', defaultHistory: [] }
  ]
}),
    CollectionsModule
    

    //BleOperatorPageModule

  ],
  bootstrap: [IonicApp],
  entryComponents: [
    /*TabsPage,
    editDevicePage,
    ModeDevicesPage,
    DevMode,editDevSettingPage,*/
    MyApp,
    
  ],
  providers: [
    AndroidPermissions,
    BLE,
    NativeStorage,
    WheelSelector,
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ToastCtrlProvider,
    BleCtrlProvider,
    BleCommandProvider,
    DevicesDataProvider,
    ScheduleDataProvider,
    AppStateProvider
    
  ]
})
export class AppModule {}



